                <el-select v-model="upgradeForm.deployArr" multiple clearable style="width:100%">
                  <el-option v-for="item in deployArrOptions" :key="item.id" :label="item.pkDesc" :value="item.pkValue"></el-option>
                </el-select>
              </el-col>
            </el-row>
          </el-form-item>
          <el-form-item :label="'灰度策略'" prop="decisionArr">
            <el-row>
              <el-col :span="24">
                <el-select v-model="upgradeForm.decisionArr" multiple clearable style="width:100%">
                  <el-option v-for="item in decisionArrOptions" :key="item.id" :label="item.pkDesc" :value="item.pkValue"></el-option>
                </el-select>
              </el-col>
            </el-row>
          </el-form-item>
        </el-form>
