<script>
  import Api from '@/constants/Api.js'
  import {mapState, mapActions} from 'vuex'
  export default {
    data() {
      return {
        that: this,
        isConfig: true, // 切换
        loading: false,
        // 查询规则
        inputRules: {
          name: [
            { required: true, message: '请输入名称', trigger: 'blur' }
          ],
          installName: [
            { required: true, message: '请输入安装名', trigger: 'blur' }
          ],
          installPath: [
            { required: true, message: '请输入安装路径', trigger: 'blur' }
          ],
          ip: [
            { required: true, message: '请输入Ip', trigger: 'blur' }
          ],
          versionNo: [
            { required: true, message: '请输入版本号', trigger: 'blur' }
          ],
          moType: [
            { required: true, message: '请输入应用类型', trigger: 'blur' }
          ],
          targerVersion: [
            { required: true, message: '请输入目标版本', trigger: 'blur' }
          ],
          deployArr: [
            { required: true, message: '请输入灰度机', trigger: 'blur' }
          ],
          decisionArr: [
            { required: true, message: '请输入灰度策略', trigger: 'blur' }
          ]
        },
        // coreList数据
        pageNo: 1, // 当前页数
        limit: 10, // 每页条数
        total: 0, // 总页数
        versionOptions: [], // coreList版本号下拉数据
        tableVal: [],
        coreForm: {}, // 查询
        tableList: [], // table原始数据
        idx: -1,
        addVisible: false,
        radio: '1',
        ifShow: false,
        listForm: {
          status: 'UNINSTALL',
          name: '',
          moType: '',
          installName: '',
          installPath: '',
          ip: '',
          versionNo: '',
          remark: ''
        }, // coreList弹出框
        listTitle: '',
        isStatus: false, // 是否能输入
        logVisible: false,
        logName: '',
        logData: [],
        // coreUpgrade
        upgradePageNo: 1, // 当前页数
        upgradeTotal: 0, // 总页数
        targerVersionOptions: [], // coreUpgrade版本号下拉数据
        deployArrOptions: [], // coreUpgrade灰度机下拉数据
        upgradeSearchForm: {},
        upgradeVisible: false,
        upgradeTitle: '添加灰度应用实例',
        upgradeForm: {
          moType: '',
          targerVersion: '',
          deployArr: [],
          decisionArr: []
        },
        upgradeTableData: []
      }
    },
    computed: {
      ...mapState({
        ipOptions: state => state.core.ipOptions,
        moTypeListOptions: state => state.core.moTypeListOptions,
        decisionArrOptions: state => state.core.decisionArrOptions
      }),
      colorTo() {
        if (this.isConfig) {
          return 'primary'
        } else {
          return 'info'
        }
      },
      colorBack() {
        if (!this.isConfig) {
          return 'primary'
        } else {
          return 'info'
        }
      }
    },
    watch: {
      'listForm.moType'(newVal) {
        if (newVal) {
          if (this.addVisible && this.listTitle === '修改应用实例') {
            this.listForm.versionNo = ''
          }
          this.$axios.all([
            this.$axios.post(Api.coreVersionNoCombox, {'moType': newVal})
          ])
          .then(this.$axios.spread((versionData) => {
            this.versionOptions = versionData.data
          }))
        }
      },
      'upgradeForm.moType'(newVal) {
        if (newVal) {
          if (this.upgradeVisible) {
            this.upgradeForm.targerVersion = ''
            this.upgradeForm.deployArr = []
          }
          this.$axios.all([
            this.$axios.post(Api.coreFindNodesCombox, {'moType': newVal}),
            this.$axios.post(Api.coreVersionNoCombox, {'moType': newVal})
          ])
          .then(this.$axios.spread((deployArrData, versionData) => {
            this.deployArrOptions = deployArrData.data.map((item) => {
              return {
                pkValue: Number(item.pkValue),
                pkDesc: item.pkDesc
              }
            })
            this.targerVersionOptions = versionData.data
          }))
        }
      }
    },
    async activated() {
      const vm = this
      if (this.moTypeListOptions.length <= 0) {
        await vm.coreGetData({vm})
        vm.search()
      } else {
        await this.search()
      }
    },
    methods: {
      ...mapActions('core', ['coreGetData']),
      // 切换
      handover(status) {
        this.isConfig = status
        if (this.isConfig) {
          this.search()
        } else {
          this.upgradeSearch()
        }
      },
      // 翻页事件
      handleCurrentChange(val) {
        if (this.isConfig) {
          this.pageNo = val
          this.search()
        } else {
          this.upgradePageNo = val
          this.upgradeSearch()
        }
      },
      search(title) {
        this.loading = true
        let {name, ip, moType} = this.coreForm
        let query = Object.assign({}, {
          'pageNo': this.pageNo,
          'limit': this.limit,
          'baseParam': {
            moType, name, ip
          }
        })
        this.$axios.post(Api.coreNodeFind, query)
          .then((searchData) => {
            if (title === 'search') {
              this.$message.success(`查询成功`)
            }
            let data = JSON.parse(JSON.stringify(searchData.data))
            this.tableList = data.data
            this.refresh(data.data)
            this.total = data.recordsTotal
            this.loading = false
          }).catch((error) => {
            this.loading = false
            this.$message.error(error.data.message || error.message || error)
          })
      },
      // 刷新
      refresh(rowVal, title) {
        let rowValData = JSON.parse(JSON.stringify(rowVal))
        let params
        let query = []
        if (Array.isArray(rowVal)) {
          params = JSON.parse(JSON.stringify(rowVal))
          rowValData.forEach(({deployId}) => {
            query.push({deployId})
          })
        } else {
          params = JSON.parse(JSON.stringify(this.tableList))
          let {deployId} = rowValData
          query.push({deployId})
        }
        this.loading = true
        this.$axios.post(Api.coreAppStatus, query).then((res) => {
          if (res.code === '0000') {
            params.map((value) => {
              res.data.forEach((item) => {
                if (item.deployId === value.deployId) {
                  value.status = item.status
                  return value
                } else {
                  return value
                }
              })
            })
            this.loading = false
            this.tableList = params
            if (title === 'search') {
              this.$message.success(`刷新成功`)
            }
          }
        }).catch((error) => {
          this.loading = false
          this.$message.error(error.data.message || error.message || error)
        })
      },
      // 新增/编辑
      handleAddEdit(title, value) {
        if (title === '添加应用实例') {
          this.listTitle = '添加应用实例'
          this.isStatus = false
          this.listForm = {
            status: 'UNINSTALL',
            name: '',
            moType: '',
            installName: '',
            installPath: '',
            ip: '',
            versionNo: '',
            remark: ''
          }
        }
        if (title === '修改应用实例') {
          this.listTitle = '修改应用实例'
          if (value.status === 'UNINSTALL') {
            this.isStatus = false
          } else {
            this.isStatus = true
          }
          this.listForm = JSON.parse(JSON.stringify(value))
        }
        this.$nextTick(() => {
          this.addVisible = true
        })
      },
      saveAddEdit() {
        let {deployId, name, ip, moType, installName, installPath, versionNo, remark, status} = JSON.parse(JSON.stringify(this.listForm))
        let query = Object.assign({}, {deployId, name, ip, moType, installName, installPath, versionNo, remark, status})
        if (this.listTitle === '添加应用实例') {
          delete query.deployId
          if (!name || !ip || !installName || !installPath || !versionNo || !status) {
            this.$message({
              message: '带*值不能为空',
              type: 'warning'
            })
            return false
          }
          this.$axios.post(Api.coreNodeAdd, query).then((res) => {
            if (res.code === '0000') {
              this.search()
              this.$message.success(`新增成功`)
              this.addVisible = false
            }
          })
        }
        if (this.listTitle === '修改应用实例') {
          delete query.status
          if (!name || !ip || !installName || !installPath || !versionNo) {
            this.$message({
              message: '带*值不能为空',
              type: 'warning'
            })
            return false
          }
          this.loading = true
          this.$axios.post(Api.coreNodeModify, query).then((res) => {
            if (res.code === '0000') {
              this.search()
              this.$message.success(`编辑成功`)
              this.addVisible = false
            }
          }).catch((error) => {
            this.loading = false
            this.$message.error(error.data.message || error.message || error)
          })
        }
      },
      cancel() {
        if (this.isConfig) {
          this.addVisible = false
          this.$refs.listForm.resetFields()
        } else {
          this.upgradeVisible = false
          this.$refs.upgradeForm.resetFields()
        }
      },
      // 部署节点
      handleInstall(value) {
        this.conmmonMethods.makePopBox(this, '部署节点').then(() => {
          let query
          let url
          let errorName = ''
          if (value) {
            query = {}
            let {deployId, moType, ip, installName, installPath, versionNo} = JSON.parse(JSON.stringify(value))
            query = Object.assign({}, {deployId, moType, ip, installName, installPath, versionNo})
            url = Api.coreAppInstall
          } else {
            if (Array.isArray(this.tableVal)) {
              query = []
              url = Api.coreAppBatchInstall
              this.tableVal.forEach((value) => {
                let {name, deployId, moType, ip, installName, installPath, versionNo, status} = value
                if (status === 'UNINSTALL') {
                  query.push({deployId, moType, ip, installName, installPath, versionNo})
                } else {
                  errorName += name + '、'
                }
              })
            }
          }
          if (errorName.length > 0) {
            this.$message({
              message: '节点名称：' + errorName.slice(0, errorName.length - 1) + '不符合该操作，请重新选择',
              type: 'warning'
            })
            return false
          }
          this.loading = true
          this.$axios.post(url, query).then((res) => {
            if (res.code === '0000') {
              this.$message({
                message: '部署成功',
                type: 'success'
              })
              this.search()
            }
          }).catch((error) => {
            this.loading = false
            this.$message.error(error.data.message || error.message || error)
          })
        })
      },
      // 卸载节点
      handleUninstall(value) {
        this.conmmonMethods.makePopBox(this, '卸载节点').then(() => {
          let query
          let url
          let errorName = ''
          if (value) {
            query = {}
            let {deployId, ip, installName, installPath, versionNo, moType} = JSON.parse(JSON.stringify(value))
            query = Object.assign({}, {deployId, ip, installName, installPath, versionNo, moType})
            url = Api.coreAppUninstall
          } else {
            if (Array.isArray(this.tableVal)) {
              query = []
              url = Api.coreAppBatchUninstall
              this.tableVal.forEach((value) => {
                let {name, deployId, ip, installName, installPath, versionNo, moType, status} = value
                if (status === 'INSTALL' || status === 'STOPPED') {
                  query.push({deployId, ip, installName, installPath, versionNo, moType})
                } else {
                  errorName += name + '、'
                }
              })
            }
          }
          if (errorName.length > 0) {
            this.$message({
              message: '节点名称：' + errorName.slice(0, errorName.length - 1) + '不符合该操作，请重新选择',
              type: 'warning'
            })
            return false
          }
          this.loading = true
          this.$axios.post(url, query).then((res) => {
            if (res.code === '0000') {
              this.$message({
                message: '卸载成功',
                type: 'success'
              })
              this.search()
            }
          }).catch((error) => {
            this.loading = false
            this.$message.error(error.data.message || error.message || error)
          })
        })
      },
      // 启动节点
      handleStart(value) {
        this.conmmonMethods.makePopBox(this, '启动节点').then(() => {
          let query
          let url
          let errorName = ''
          if (value) {
            query = {}
            let {moType, ip, installName, installPath} = JSON.parse(JSON.stringify(value))
            query = Object.assign({}, {moType, ip, installName, installPath})
            url = Api.coreAppStart
          } else {
            if (Array.isArray(this.tableVal)) {
              query = []
              url = Api.coreAppBatchStart
              this.tableVal.forEach((value) => {
                let {moType, name, ip, installName, installPath, status} = value
                if (status === 'INSTALL' || status === 'STOPPED') {
                  query.push({moType, ip, installName, installPath})
                } else {
                  errorName += name + '、'
                }
              })
            }
          }
          if (errorName.length > 0) {
            this.$message({
              message: '节点名称：' + errorName.slice(0, errorName.length - 1) + '不符合该操作，请重新选择',
              type: 'warning'
            })
            return false
          }
          this.loading = true
          this.$axios.post(url, query).then((res) => {
            if (res.code === '0000') {
              this.$message({
                message: '启动成功',
                type: 'success'
              })
              this.search()
            }
          }).catch((error) => {
            this.loading = false
            this.$message.error(error.data.message || error.message || error)
          })
        })
      },
      // 停止节点
      handleStop(value) {
        this.conmmonMethods.makePopBox(this, '停止节点').then(() => {
          let query
          let url
          let errorName = ''
          if (value) {
            let {moType, ip, installName, installPath} = JSON.parse(JSON.stringify(value))
            query = Object.assign({}, {moType, ip, installName, installPath})
            url = Api.coreAppStop
          } else {
            if (Array.isArray(this.tableVal)) {
              query = []
              url = Api.coreAppBatchStop
              this.tableVal.forEach((value) => {
                let {moType, name, ip, installName, installPath, status} = value
                if (status === 'RUNNING') {
                  query.push({moType, ip, installName, installPath})
                } else {
                  errorName += name + '、'
                }
              })
            }
          }
          if (errorName.length > 0) {
            this.$message({
              message: '节点名称：' + errorName.slice(0, errorName.length - 1) + '不符合该操作，请重新选择',
              type: 'warning'
            })
            return false
          }
          this.loading = true
          this.$axios.post(url, query).then((res) => {
            if (res.code === '0000') {
              this.$message({
                message: '停止成功',
                type: 'success'
              })
              this.search()
            }
          }).catch((error) => {
            this.loading = false
            this.$message.error(error.data.message || error.message || error)
          })
        })
      },
      // 删除
      deleteRow(value = null) {
        this.conmmonMethods.makePopBox(this, '删除').then(() => {
          if (value) {
            let {deployId, status} = value
            if (status === 'UNINSTALL') {
              this.loading = true
              this.$axios.delete(Api.coreNodeDelete + deployId).then((res) => {
                if (res.code === '0000') {
                  this.search()
                  this.$message.success(`删除成功`)
                }
              }).catch((error) => {
                this.loading = false
                this.$message.error(error.data.message || error.message || error)
              })
            } else {
              this.$message({
                message: '未部署的节点才能被删除',
                type: 'warning'
              })
            }
          } else {
            let query = []
            let errorName = ''
            this.tableVal.forEach((value) => {
              let {name, deployId, status} = value
              if (status === 'UNINSTALL') {
                query.push({deployId})
              } else {
                errorName += name + '、'
              }
            })
            if (errorName.length > 0) {
              this.$message({
                message: '节点名称：' + errorName.slice(0, errorName.length - 1) + '不符合该操作，请重新选择',
                type: 'warning'
              })
              return false
            }
            this.loading = true
            this.$axios.post(Api.coreNodeBatchDelete, query).then((res) => {
              this.$message.success(`删除成功`)
              this.search()
            }).catch((error) => {
              this.loading = false
              this.$message.error(error.data.message || error.message || error)
            })
          }
        })
      },
      // 监控
      moniter(scope) {
        let query = {
          moType: 'Galaxy',
          ip: scope.row.ip,
          path: scope.row.installPath + '/' + scope.row.installName
        }
        this.$router.push({name: 'coreAppEcharts', query: query, params: {'id': scope.row.ip}})
      },
      // coreList日志
      viewLog(value) {
        this.logVisible = true
        let {name, moType, ip, installName, installPath} = (value)
        this.logName = name
        let query = Object.assign({}, {moType, ip, installName, installPath})
        this.$axios.post(Api.coreAppLog, query).then((res) => {
          this.logData = res && res.data && JSON.parse(JSON.stringify(res.data))
        }).catch((error) => {
          this.$message.error(error.data.message || error.message || error)
        })
      },
      // 全选
      handleSelectionChange(val) {
        this.tableVal = val
      },
      // 部署选择
      radioChange(val) {
        this.listForm.status = val
      },
      // Ip下拉选择
      ipSearch(value, cb) {
        let query = this.ipOptions
        let results = value ? query.filter((item) => {
          return (item.value.toLowerCase().indexOf(value.toLowerCase()) === 0)
          }) : query
        cb(results)
      },
      // search
      upgradeSearch(title) {
        this.loading = true
        let {name} = this.upgradeSearchForm
        let query = Object.assign({}, {
          'pageNo': this.upgradePageNo,
          'limit': this.limit,
          'baseParam': {
            'moType': 'Galaxy', name
          }
        })
        this.$axios.post(Api.coreUpgradeJob, query).then((searchData) => {
          if (title === 'search') {
            this.$message.success(`查询成功`)
          }
          let data = JSON.parse(JSON.stringify(searchData.data))
          this.upgradeTableData = data.data
          this.upgradeTotal = data.recordsTotal
          this.loading = false
        }).catch((error) => {
          this.loading = false
          this.$message.error(error.data.message || error.message || error)
        })
      },
      // 新增/编辑
      upgradeAddEdit(value) {
        if (value) {
          this.upgradeTitle = '修改灰度应用实例'
          this.upgradeForm = JSON.parse(JSON.stringify(value))
        } else {
          this.upgradeTitle = '添加灰度应用实例'
          this.upgradeForm = {
            moType: '',
            targerVersion: '',
            deployArr: [],
            decisionArr: []
          }
        }
        this.$nextTick(() => {
          this.upgradeVisible = true
        })
      },
      // 新增/编辑-确定
      upgradeSaveAddEdit() {
        this.loading = true
        let url
        let {id, name, moType, targerVersion, deployArr, decisionArr} = this.upgradeForm
        if (!name || !moType || !targerVersion || deployArr === [] || decisionArr === []) {
          this.$message.warning(`带*不能为空`)
          this.upgradeVisible = false
          return false
        }
        let query = Object.assign({
          createUser: window.localStorage.getItem('userName')
        }, {id, name, moType, targerVersion, deployArr, decisionArr})
        if (this.upgradeTitle === '添加灰度应用实例') {
          url = Api.coreAddUpgradeJob
        } else {
          url = Api.coreUpdateUpgradeJob
        }
        this.$axios.post(url, query).then((searchData) => {
            this.upgradeVisible = false
            this.upgradeSearch()
          }).catch((error) => {
            this.loading = false
            this.$message.error(error.data.message || error.message || error)
          })
      },
      // 删除
      upgradeDelete(value) {
        this.conmmonMethods.makePopBox(this, '删除').then(() => {
          let {id} = value
          this.loading = true
          this.$axios.post(Api.coreDeleteUpgradeJob, {id}).then((res) => {
            if (res.code === '0000') {
              this.upgradeSearch()
              this.$message.success(`删除成功`)
            }
          }).catch((error) => {
            this.loading = false
            this.$message.error(error.data.message || error.message || error)
          })
        })
      },
      upgradeHandle(value, it) {
        let box = new Map([
          ['upgrade', () => {
            return {
              url: Api.coreUpgradeHandle,
              title: '升级'
            }
          }],
          ['publish', () => {
            return {
              url: Api.coreUpgradeHandle,
              title: '余量发布'
            }
          }],
          ['rollback', () => {
            return {
              url: Api.coreUpgradeBackInstant,
              title: '回退'
            }
          }]
        ])
        this.conmmonMethods.makePopBox(this, box.get(it)().title).then(() => {
          let {id, moType, deployArr, decisionArr, status} = value
          this.loading = true
          this.$axios.post(box.get(it)().url, {id, moType, deployArr, decisionArr, status}).then((res) => {
            if (res.code === '0000') {
              this.upgradeSearch()
              this.$message.success(`${box.get(it)().title}成功`)
            }
          }).catch((error) => {
            this.loading = false
            this.$message.error(error.data.message || error.message || error)
          })
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
  .name-state {
    margin-right: 10px;
    font-size: 18px;
    font-weight: bolder;
  }
</style>



